@use "global" as *;

.p-schedule {
  background: #f0f3f7;
  padding-top: rem(100);
  padding-bottom: rem(183);
}
.p-schedule__content {
}
.p-schedule__title {
  color: $blue;
  font-size: rem(15);
  font-weight: 700;
  letter-spacing: 0.06em;
}
.p-schedule__cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: rem(24);
  @include mq(md) {
    grid-template-columns: 1fr 1fr 1fr;
    gap: rem(82.5);
    margin-top: rem(72);
    position: relative;
  }
}
