.p-schedule__cards::before {
  content: "";
  position: absolute;
  background: $white;
  width: rem(806);
  height: rem(18);
  border-radius: rem(10);
  top: 45%;
  left: 50%;
  transform: translateX(-50%);
}
.p-schedule__card {
  position: relative;
}
.p-schedule__card:first-child::after {
  position: absolute;
  content: "";
  right: -35%;
  top: 32%;
  width: rem(160);
  height: rem(18);
  background: $white;
}
.p-schedule__card:nth-child(2)::after {
  position: absolute;
  content: "";
  right: -35%;
  top: 32%;
  width: rem(160);
  height: rem(18);
  background: $white;
}
.p-schedule__card:nth-child(3)::after {
  position: absolute;
  content: "";
  left: 50%;
  top: 32%;
  transform: translateX(-50%);
  width: rem(18);
  height: rem(260);
  background: $white;
  z-index: 0;
}
.p-schedule__card:nth-child(4)::after {
  position: absolute;
  content: "";
  left: 50%;
  top: -30%;
  transform: translateX(-50%);
  width: rem(18);
  height: rem(255);
  background: $white;
  z-index: 0;
  border-radius: rem(10);
}
.p-schedule__card:nth-child(5)::after {
  position: absolute;
  content: "";
  left: -35%;
  top: 32%;
  width: rem(160);
  height: rem(18);
  background: $white;
}
.p-schedule__card:nth-child(6)::after {
  position: absolute;
  content: "";
  left: -35%;
  top: 32%;
  width: rem(160);
  height: rem(18);
  background: $white;
}
.p-schedule__time {
  position: absolute;
  color: $blue;
  background: $white;
  font-size: rem(19.5);
  width: rem(77.25);
  display: inline-block;
  padding-top: rem(9);
  padding-bottom: rem(9);
  text-align: center;
  border: 1px solid $blue;
  font-weight: 700;
  letter-spacing: 0.06em;
  margin-top: rem(-18);
  margin-left: rem(9);
  z-index: 2;
}

.p-schedule__card:nth-child(n + 4) {
  @include mq(md) {
    margin-top: rem(60);
  }
}
.p-schedule__img {
  width: 100%;
  position: relative;
  z-index: 1;
}
.p-schedule__img::before {
  padding-top: 67.6557%;
  display: block;
  content: "";
}
.p-schedule__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.p-schedule__text {
  margin-top: rem(16);
  position: relative;
  z-index: 1;
}
.p-schedule__bottom-img {
  width: 100%;
  position: relative;
}
.p-schedule__bottom-img::before {
  padding-top: 33.854%;
  display: block;
  content: "";
}
.p-schedule__bottom-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
